<style lang="less" scoped >
.projectInfo {
  width: 900px;
  margin: 0 auto;
  border: 1px solid #999;
  text-align: left;
  padding: 15px 30px;
  margin-top: 40px;
  .input {
    width: 180px;
    display: inline-block;
    padding-right: 30px;
  }
  .input1 {
    padding-top: 10px;
    width: 300px;
  }
  .el-form-item {
    margin: 0;
  }
  .text {
    vertical-align: middle;
    font-size: 14px;
    color: #606266;
    line-height: 40px;
    padding: 0 12px 0 0;
  }
  .title-text {
    font-size: 16px;
    padding: 5px 0;
  }
  .dialog-footer {
    text-align: right;
    padding-top: 20px;
  }
}
</style>
<template>
  <div class='projectInfo'>
    <div class='title-text'></div>
    <el-form :model="submitForm" class='form' :disabled="isDisable()">
      <div>
        <el-form-item label="" class='input'>
          <el-input v-model="submitForm.project_code" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" class='input'>
          <el-input v-model="submitForm.project_name" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </div>

      <!--  -->
      <div class="text"></div>
      <el-form-item prop="property_tags">
        <el-checkbox v-model="submitForm.property_tags" v-for="item in typeOptions" :key="item.param_id" :label="item.param_id">{{item.param}}</el-checkbox>
      </el-form-item>
      <div class='text'></div>
      <el-form-item>
        <city-selector :province.sync="submitForm.province_name" :city.sync="submitForm.city_name" :district.sync="submitForm.district_name" />
        <el-input class='input1' v-model="submitForm.absolute_address"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <template slot-scope="scope">
          {{state(submitForm.state)}}</template>
      </el-form-item><br/>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.project_hold_name" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.project_hold_phone" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.developer_name" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.statement_company" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.subordinate_company" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="" class='input'>
        <el-input v-model="submitForm.company_relation" auto-complete="off"></el-input>
      </el-form-item>
    </el-form>
    <!-- <div class='title-text'></div>
        <el-table :data="gridData" border>
            <el-table-column  type="selection"></el-table-column>
            <el-table-column prop="date" label="" align='center'></el-table-column>
            <el-table-column prop="name" label="" align='center'></el-table-column>
            <el-table-column prop="address" label="" align='center'></el-table-column>
            <el-table-column prop="address" label="" align='center'></el-table-column>
            <el-table-column prop="address" label="" align='center'></el-table-column>
            <el-table-column prop="address" label="" align='center'></el-table-column>
            <el-table-column prop="address" label="" align='center'></el-table-column>
            <el-table-column label="" align='center'>
                <template slot-scope="scope">
                <el-button></el-button>
            </template>
            </el-table-column>
        </el-table> -->
    <div class='title-text'></div>
    <el-form :model="submitForm.auditProject">
      <div>
        <el-form-item label="" class='input'>
          <el-input v-model="submitForm.fp_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" class='input'>
          <el-input v-model="submitForm.fp_time" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" class='input'>
          <el-input v-model="submitForm.state" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" class='input'>
          <el-input v-model="submitForm.remark" auto-complete="off"></el-input>
        </el-form-item>
      </div>
    </el-form>
    <div class='title-text'></div>
    <el-table :data="submitForm.peopleInfo" border>
      <el-table-column property="nub" label="" align='center' width="70px"></el-table-column>
      <el-table-column property="company_name" label="" align='center'></el-table-column>
      <el-table-column property="company_relation" label="" align='center'></el-table-column>
      <el-table-column property="s_time" label="" align='center'></el-table-column>
      <el-table-column property="e_time" label="" align='center'></el-table-column>
      <el-table-column property="project_hold_name" label="" align='center'></el-table-column>
      <el-table-column property="project_hold_phone" label="" align='center'></el-table-column>
      <el-table-column property="project_hold_phone" label="" align='center'></el-table-column>
      <el-table-column property="project_hold_phone" label="" align='center'></el-table-column>
      <el-table-column property="project_hold_phone" label="" align='center'></el-table-column>
      <el-table-column property="project_hold_phone" label="" align='center'></el-table-column>
    </el-table>
    <div slot="footer" class="dialog-footer">
      <el-button @click='cancel'> </el-button>
      <el-button type="primary" @click='submit' :disabled="isDisable()"> </el-button>
    </div>
  </div>
</template>
<script>
import CitySelector from "../../../components/CitySelector";
export default {
  data() {
    return {
      submitForm: {
        province: "",
        city: "",
        district: "",
        project_id: "",
        auditProject: {},
        peopleInfo: [],
        property_tags: []
      },
      project_id: "",
      typeOptions: []
    };
  },
  mounted() {
    if (this.$route.params.project_id) {
      this.operationType = this.$route.params.operationType;
      this.project_id = this.$route.params.project_id;
      this.getProjectDetail();
      this.getType();
    } else {
      this.$router.push({ name: "startApply" });
    }
  },
  methods: {
    async getType() {
      let res = await this.api.getTags();
      if (res.code == 200) {
        this.typeOptions = res.data;
      }
    },
    state(row) {
      if (row == 1) {
        return "";
      } else if (row == 0) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 3) {
        return "";
      }
    },
    isDisable() {
      if (this.operationType == 0) {
        return true;
      } else {
        return false;
      }
    },
    async getProjectDetail() {
      Object.assign(this.submitForm, this.$route.params.projectInfo);
      let res = await this.api.applyProject({ project_id: this.project_id });
      if (res.code == 200) {
          Object.assign(this.submitForm, res.data);
          let property_tags = res.data.property_tags;
          let arr = [];
          for (let type of property_tags) {
            arr.push(type.property_tag_id);
          }
          this.submitForm.property_tags = arr;
      }
    },
    async search() {
      let res = await this.api.changeProjectList({
        project_id: this.submitForm.project_id
      });
      if (res.code == 200) {
        this.sumbitForm = res.data;
      }
    },
    async submit() {
      let res = this.api.changeProjectAdd({
        project_id: this.submitForm.project_id
      });
      if (res.code == 200) {
        this.$router.push({ name: "distribution" });
      }
    },
    cancel() {
      if (this.$route.params.operationType != undefined) {
        this.$router.push({ name: "distribution" });
      } else {
        this.$router.push({ name: "startApply" });
      }
    }
  },
  components: {
    CitySelector: CitySelector
  }
};
</script>



