<style lang="less" scoped src='./index.less'>
</style>
<style lang="less">
.quitPeople {
  .el-table th {
    padding: 8px 5px;
  }
  .el-table td {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0 20px 20px 20px;
    margin: 0;
  }
}
</style>

<template>
  <div class='quitPeople'>
    <div class='title'>
      <div class='left'>
        <div class='text1'></div>
        <div class="search-block">
          <el-input class='query'></el-input>
          <el-button icon="el-icon-search" circle></el-button>
        </div>
      </div>

    </div>
    <template>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="80px"> </el-table-column>
        <el-table-column prop="account" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="name" label="" align='center'></el-table-column>
        <el-table-column prop="tel" label="" align='center' width="120px"></el-table-column>
        <el-table-column prop="project_name" label="" align='center'></el-table-column>
        <el-table-column prop="role" label="" align='center'>
          <template slot-scope="scope">{{getRole(scope.row.role)}}</template>
        </el-table-column>
        <el-table-column prop="department" label="" align='center'></el-table-column>
        <el-table-column prop="position" label="" align='center'></el-table-column>
        <el-table-column prop="city" label="" align='center'></el-table-column>
        <el-table-column prop="district" label="" align='center'></el-table-column>
        <el-table-column prop="entry_time" label="" align='center' width="100px"></el-table-column>
        <el-table-column prop="create_time" label="" align='center' width="100px"></el-table-column>
        <el-table-column prop="remark" label="" align='center' width="200px"></el-table-column>
        <el-table-column label="" align='center'>
          <template slot-scope="scope">
            <el-button type="text" @click='showSee(scope.row)'></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="" :visible.sync="dialogFormVisible" class='dialog' @close="cancel">
        <div class='btn'>
          <el-button type='primary' @click='cancel'></el-button>
        </div>
        <div class='num_set'></div>
        <el-form v-model="examinePeople">
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>
              {{role(examinePeople.role)}}
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.account}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.department}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.position}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border  img'>
              <img src="../../../assets/images/head.png" />
              <!-- <img :src="'http://120.27.21.136:2798/' + examinePeople.img_url" /> -->
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.entry_time}}</div>
          </el-form-item>
          <div class='num_set'></div>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>
              {{sex(examinePeople.sex)}}
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.tel}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.birth}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.city_name+examinePeople.district_name+examinePeople.absolute_address}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.id_card}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border  img'>
              <img src="../../../assets/images/idCard.png" />
              <!-- <img :src="'http://120.27.21.136:2798/' + examinePeople.card_url" /> -->
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.bank_name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.bank_card}}</div>
          </el-form-item>
          <el-form-item class='input2'>
            <div></div>
            <div class='border height'>{{examinePeople.slef_desc}}</div>
          </el-form-item>
          <div class='num_set'></div>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.quit ? examinePeople.quit.create_time : ''}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.quit ? examinePeople.quit.type : ''}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border height'>{{examinePeople.quit ? examinePeople.quit.remark : ''}}</div>
          </el-form-item>
        </el-form>
      </el-dialog>
    </template>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      dialogFormVisible: false,
      examinePeople: {
        name: "",
        role: "",
        account: "",
        department: "",
        position: "",
        img_url: "",
        entry_time: "",
        project_name: "",
        sex: "",
        tel: "",
        birth: "",
        absolute_address: "",
        slef_desc: "",
        bank_card: "",
        bank_name: "",
        card_url: "",
        id_card: "",
        quit: {
          his_id: "",
          auth_id: "",
          create_time: "",
          type: "",
          remark: ""
        }
      }
    };
  },
  mounted() {
    this.getQuitList();
  },
  methods: {
    getRole(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    async showSee(row) {
      this.dialogFormVisible = true;
      Object.assign(this.examinePeople, row);
      this.examinePeople.agent_id = row.agent_id;
      let res = await this.api.getQuitInfo(this.examinePeople);
      if (res.code == 200) {
        this.examinePeople = res.data;
      }
    },
    auditingState(row) {
      if (row == 0) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    role(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    sex(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 0) {
        return "";
      }
    },
    cancel() {
      this.dialogFormVisible = false;
    },
    async getQuitList() {
      let res = await this.api.getQuitList();

      if ((res.code = 200)) {
        this.tableData = res.data.data;
      }
    }
  }
};
</script>





