<style lang="less" scoped>
.companyPriceInfo {
  .table {
    margin: 50px 100px 50px 200px;
    .text {
      font-size: 15px;
      text-align: left;
      padding-bottom: 10px;
      .price {
        padding-right: 20px;
      }
    }
    .title {
      position: relative;
      padding-bottom: 20px;
      text-align: left;
      .title-text {
        padding-right: 360px;
        font-size: 20px;
        padding-bottom: 15px;
      }
      .pos-btn {
        position: absolute;
        top: 0;
        right: 0;
      }
    }
    .query {
      width: 300px;
    }
  }
}
</style>
<style lang="less">
.companyPriceInfo {
  .el-table--border,
  .el-table--group {
    margin-top: 20px;
  }
  .el-table th {
    padding: 8px 0px;
  }
  .el-table td {
    padding: 5px 0;
  }
  .el-dialog__body {
    padding-top: 0;
  }
}
</style>
<template>
    <div class='companyPriceInfo'>
        <div class='table'>
            <div class='title'>
                <span class='title-text'></span>
                <el-input v-model="searchObj.search" class='query' placeholder="/"></el-input>
                <el-button @click="getCompanyAllInfo" icon="el-icon-search" circle></el-button>
                <el-button class='pos-btn' type="primary" @click='cancel'></el-button>
            </div>
            <div class='text'>
                <span class='price'>:{{sumbitForm.total_price}}</span>
                <span class='price'>:{{sumbitForm.count}}</span>
            </div>
            <el-table :data="tableData" border style="width: 100%">
                <el-table-column prop="" label="" align='center' width="70px"></el-table-column>
                <el-table-column prop="client_id" label="" align='center'></el-table-column>
                <el-table-column prop="project_name" label="" align='center'></el-table-column>
                <el-table-column prop="house_info" label="" align='center'></el-table-column>
                <el-table-column prop="name" label="" align='center' width="110px"></el-table-column>
                <el-table-column prop="tel" label="" align='center'></el-table-column>
                <el-table-column prop="broker_type" label="" align='center'>
                    <template slot-scope="scope">{{brokerType(scope.row.broker_type)}}</template>
                </el-table-column>
                <el-table-column prop="create_time" label="" align='center' width="160px"></el-table-column>
                <el-table-column prop="broker_num" label="" align='center'></el-table-column>
                <el-table-column prop="commission_way" label="" align='center'></el-table-column>
                <el-table-column prop="pay_state" label="" align='center'>
                    <template slot-scope="scope">{{state(scope.row.pay_state)}}</template>
                </el-table-column>
            </el-table>

        </div>

    </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      project_id: "",
      company_rule_id: "",
      searchObj: {
        search: ""
      },
      sumbitForm: {
        total_price: "",
        count: ""
      }
    };
  },
  mounted() {
    this.project_id = this.$route.params.project_id;
    if (!this.project_id) {
      this.$router.push({ name: "companyPrice" });
      return;
    }
    this.company_rule_id = this.$route.params.company_rule_id;
    this.getCompanyAllInfo();
  },
  methods: {
    async getCompanyAllInfo() {
      let res = await this.api.getCompanyAllInfo({
        project_id: this.project_id,
        company_rule_id: this.company_rule_id,
        search: this.searchObj.search
      });
      if (res.code == 200) {
        this.tableData = res.data.data;
        this.sumbitForm.total_price = res.data.data.total_price;
        this.sumbitForm.count = res.data.data.count;
      }
    },
    cancel() {
      this.$router.push({
        name: "companyPrice",
        params:this.$route.params,
      });
    },
    brokerType(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 3) {
        return "";
      }
    },
    state(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 0) {
        return "";
      }
    }
  }
};
</script>



