<style lang="less" scoped src="./index.less" ></style>
<style lang="less">
.project {
  .el-form-item__label {
    float: none;
  }
  .el-dialog__body {
    padding-top: 0;
  }
  .el-dialog {
    width: 63%;
    top: -10%;
  }
  .el-table--border,
  .el-table--group {
    margin-top: 20px;
  }
  .el-table th {
    padding: 8px 0px;
  }
  .el-table td {
    padding: 0;
  }
}
</style>
<template>
  <div class='box project'>
    <div class='title'>
      <div class='left'>
        <div class='text1'></div>
        <div class="tab-block">
          <el-button class='tip' type="text" @click="search(0)"></el-button>
          <el-button class='tip' type="text" @click="search(1)"></el-button>
          <el-button class='tip' type="text" @click="search(2)"></el-button>
          <el-button class='tip' type="text" @click="search(3)"></el-button>
          <el-button class='tip' type="text" @click="search(4)"></el-button>
          <el-button class='tip' type="text" @click="search(5)"></el-button>
          <div class="tab-block-inner">
            <el-input v-model="searchObj.search" class='query'></el-input>
            <el-button @click="getProjectList" icon="el-icon-search" circle></el-button>
               <el-button type="primary" @click='showAdd(0)'></el-button>
          </div>
        </div>
      </div>
      <span class='right'>
      </span>
    </div>
    <template>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="70px"> </el-table-column>
        <el-table-column prop="project_code" label="" align='center' width="100px"></el-table-column>
        <el-table-column prop="project_name" label="" align='center' width="120px"></el-table-column>
        <!--  -->
        <el-table-column prop="state" label="" align='center' width="90px">
          <template slot-scope="scope">{{projectState(scope.row.state)}}</template>
        </el-table-column>
        <el-table-column prop="city" label="" align='center' width="90px"></el-table-column>
        <!-- <el-table-column  prop="absolute_address" label="" align='center' width="220px"></el-table-column> -->
        <el-table-column prop="developer_name" label="" align='center'></el-table-column>
        <el-table-column prop="company_relation" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="subordinate_company" label="" align='center' width="120px"></el-table-column>
        <el-table-column prop="source" label="" align='center' width="80px">
          <template slot-scope="scope">{{scopeState (scope.row.source)}}</template>
        </el-table-column>
        <el-table-column prop="authentication_state" label="" align='center' width="90px">
          <template slot-scope="scope">{{authenticationState (scope.row.authentication_state)}}</template>
        </el-table-column>
        <!-- <el-table-column  prop="" label="" align='center' width="120px"></el-table-column> -->
        <!-- <el-table-column  prop="create_time" label="" align='center' width="120px"></el-table-column> -->
        <!--  -->
        <el-table-column prop="auditing_state" label="" align='center' width="90px">
          <template slot-scope="scope">{{auditingState(scope.row.auditing_state)}}</template>
        </el-table-column>
        <!-- <el-table-column  prop="auditing_name" label="" align='center' width="90px"></el-table-column> -->
        <el-table-column prop="auditing_time" label="" align='center' width="120px"></el-table-column>
        <el-table-column label="" align='center' width="180px">
          <template slot-scope="scope">
            <el-button type="text" @click='showAdd(2, scope.row)'></el-button>
            <el-button type="text" @click='showAdd(1, scope.row)' v-if="scope.row.auditing_state==1"></el-button>
            <el-button type="text" @click='showAdd(3, scope.row)' v-if="scope.row.auditing_state==2"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchObj: {
        search: "",
        tag_search: ""
      },
      tableData: [],
      operationType: 0 //0   1 
    };
  },
  mounted() {
    this.getProjectList();
  },
  methods: {
    search(type) {
      this.searchObj.tag_search = type;
      this.getProjectList();
    },
    async getProjectList() {
      let res = await this.api.getProjectList(this.searchObj);
      if (res.code == 200) {
        this.tableData = res.data;
      }
    },
    projectState(row) {
      if (row == 0) {
        return "";
      } else if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 3) {
        return "";
      }
    },
    authenticationState(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    scopeState(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    auditingState(row) {
      if (row == 0) {
        return "";
      } else if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },

    async showAdd(type, row) {
      // 0  1  2
      if (type == 0) {
        this.$router.push({
          name: "addProject",
          params: { operationType: type }
        });
      } else if (type == 1) {
        this.$router.push({
          name: "addProject",
          params: { operationType: type, project_id: row.project_id }
        });
      } else if (type == 2) {
        this.$router.push({
          name: "addProject",
          params: { operationType: type, project_id: row.project_id }
        });
      } else if (type == 3) {
        this.$router.push({
          name: "reCreateProject",
          params: { operationType: type, project_id: row.project_id }
        });
      }
    }
  }
};
</script>





