<style lang="less" scoped src="./index.less" ></style>
<style lang="less">
.agent {
  margin-left: 180px;
  margin-top: 150px;
  .el-table th {
    padding: 8px 5px;
  }
  .el-table td {
    padding: 0;
  }
  .title {
    background-color: #fafafc;
    text-align: left;
    padding-left: 180px;
    position: fixed;
    top: 0;
    left: 0;
    height: 100px;
    width: 100%;
    z-index: 99;

    .left {
      text-align: left;
      .text1 {
        padding-top: 10px;
        margin: 10px 0 12px 20px;
      }
    }
  }
  .el-dialog__body {
    padding: 0px 20px 20px 20px;
  }
  .showImg {
    width: 800px;
    position: fixed;
    top: 20%;
    left: 30%;
  }
}
</style>

<template>
  <div class='agent'>
    <div class='title'>
      <div class='left'>
        <div class='text1'></div>
        <div class="search-block">
          <el-input v-model="searchObj.name" class='query'></el-input>
          <el-button @click="search" icon="el-icon-search" circle></el-button>
        </div>
      </div>
    </div>
    <template>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="80px"> </el-table-column>
        <el-table-column prop="account" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="name" label="" align='center'></el-table-column>
        <el-table-column prop="tel" label="" align='center'></el-table-column>
        <el-table-column prop="project_name" label="" align='center'></el-table-column>
        <el-table-column prop="role" label="" align='center' width="100px">
          <template slot-scope="scope">{{getRole(scope.row.role)}}</template>
        </el-table-column>
        <el-table-column prop="department" label="" align='center' width="100px"></el-table-column>
        <el-table-column prop="position" label="" align='center'></el-table-column>
        <el-table-column prop="city_name" label="" align='center' width="100px"></el-table-column>
        <el-table-column prop="district_name" label="" align='center' width="100px"></el-table-column>
        <el-table-column prop="entry_time" label="" align='center'></el-table-column>
        <el-table-column label="" align='center'>
          <template slot-scope="scope">
            <el-button type="text" @click='showQuit(scope.row)'></el-button>
            <el-button type="text" @click='showSee(scope.row)'></el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>
    <el-dialog title="" :visible.sync="dialogFormVisible" class='dialog' @close="cancel">
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="" prop="remark">
          <el-input v-model="form.remark" class='textarea' type="textarea" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="sumbit"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="showInfo" class='dialog'>
      <div class='btn'>
        <el-button type='primary' @click='showCancel'></el-button>
      </div>
      <div class='num_set'></div>
      <el-form v-model="examinePeople">
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.name}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>
            {{role(examinePeople.role)}}
          </div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.account}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.department}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.position}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border  img'>
            <img src="../../assets/images/head.png" />
            <!-- <img :src="'http://120.27.21.136:2798/' + examinePeople.img_url" /> -->
          </div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.entry_time}}</div>
        </el-form-item>
        <div class='num_set'></div>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>
            {{sex(examinePeople.sex)}}
          </div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.tel}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.birth}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.city_name+examinePeople.district_name+examinePeople.absolute_address}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.id_card}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border  img'>
            <img src="../../assets/images/idCard.png" />
            <!-- <img :src="'http://120.27.21.136:2798/' + examinePeople.card_url" /> -->
          </div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.bank_name}}</div>
        </el-form-item>
        <el-form-item class='input1'>
          <div></div>
          <div class='border'>{{examinePeople.bank_card}}</div>
        </el-form-item>
        <el-form-item class='input2'>
          <div></div>
          <div class='border height'>{{examinePeople.slef_desc}}</div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rules: {
        remark: [
          { required: true, message: "", change: "change" }
        ]
      },
      searchObj: {
        name: ""
      },
      tableData: [],
      dialogFormVisible: false,
      showInfo: false,
      examinePeople: {
        name: "",
        role: "",
        account: "",
        department: "",
        position: "",
        img_url: "",
        entry_time: "",
        project_name: "",
        sex: "",
        tel: "",
        birth: "",
        tabsolute_addressel: "",
        slef_desc: "",
        bank_card: "",
        bank_name: "",
        card_url: "",
        id_card: ""
      },
      form: {
        id: null,
        remark: ""
      }
    };
  },
  mounted() {
    this.search();
  },
  methods: {
    getRole(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    async sumbit() {
      this.$refs["form"].validate(async valid => {
        if (valid) {
          let res = await this.api.quitPeople(this.form);
          if (res.code == 200) {
            this.$message({ type: "success", message: "" });
            this.search();
            this.cancel();
          }
        } else {
          this.cancel();
          return false;
        }
      });
    },
    async showSee(row) {
      this.showInfo = true;
      Object.assign(this.examinePeople, row);
      this.examinePeople.agent_id = row.agent_id;
      let res = await this.api.getPayrollInfo(this.examinePeople);
      if (res.code == 200) {
        this.examinePeople = res.data;
      }
    },
    role(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    sex(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 0) {
        return "";
      }
    },
    showQuit(row) {
      this.form.id = row.id;
      this.dialogFormVisible = true;
    },
    showCancel() {
      this.showInfo = false;
    },
    cancel() {
      Object.assign(this.form, this.$options.data()["form"]);
      this.dialogFormVisible = false;
    },
    async search() {
      let res = await this.api.getPayrollList(this.searchObj);
      if ((res.code = 200)) {
        this.tableData = res.data.data;
      }
    }
  }
};
</script>





