<style lang="less" scoped src="./index.less" ></style>
<style lang="less">
.examinePeople {
  .el-table th {
    padding: 8px 5px;
  }
  .el-table td {
    padding: 0;
  }
  .dialog {
    text-align: left;
    position: fixed;
    top: -10%;
    left: 0;
    .el-dialog__title {
      font-size: 26px;
    }
    .el-dialog__body {
      padding: 0px 20px 20px 20px;
    }
    .el-form-item {
      margin: 0;
    }
    .btn {
      position: absolute;
      right: 0;
      top: 15px;
      margin-right: 20px;
    }
    .num_set {
      font-size: 18px;
      padding: 5px 0;
    }
    .border {
      width: 200px;
      height: 35px;
      border: 1px solid#bbb;
      border-radius: 6px;
      padding-left: 10px;
    }
    .border.img {
      border: none;
    }
    .el-form-item__content {
      text-align: left;
    }
    .border.img {
      width: 240px;
      border: none;
      .text {
        padding-right: 20px;
        font-size: 14px;
      }
    }
    .border.height {
      height: 70px;
      width: 400px;
    }
    .border.width {
      width: 340px;
    }
    .input1 {
      display: inline-block;
      padding-right: 30px;
      text-align: left;
      vertical-align: top;
    }
    .img-text {
      display: inline-block;
      vertical-align: top;
    }
    .showImg {
      width: 800px;
      position: fixed;
      top: 20%;
      left: 30%;
    }
  }
}
</style>

<template>
  <div class='examinePeople'>
    <div class='title'>
      <div class='left'>
        <div class='text1'></div>
        <div class="left">
          <el-button class='tip' type="text" @click="search(1)"></el-button>
          <el-button class='tip' type="text" @click="search(2)"></el-button>
          <el-button class='tip' type="text" @click="search(0)"></el-button>
          <div class="search-block">
            <el-input class='query'></el-input>
            <el-button icon="el-icon-search" circle></el-button>
          </div>
        </div>
      </div>
    </div>
    <template>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="" label="" align='center' width="80px"> </el-table-column>
        <el-table-column prop="account" label="" align='center' width="110px"></el-table-column>
        <el-table-column prop="name" label="" align='center'></el-table-column>
        <el-table-column prop="project_name" label="" align='center'></el-table-column>
        <el-table-column prop="role" label="" align='center'>
          <template slot-scope="scope">{{getRole(scope.row.role)}}</template>
        </el-table-column>
        <el-table-column prop="department" label="" align='center'></el-table-column>
        <el-table-column prop="position" label="" align='center'></el-table-column>
        <el-table-column prop="city_name" label="" align='center'></el-table-column>
        <el-table-column prop="district_name" label="" align='center'></el-table-column>
        <el-table-column prop="tel" label="" align='center' width="120px"></el-table-column>
        <el-table-column prop="state" label="" align='center'>
          <template slot-scope="scope">{{auditingState(scope.row.state)}}</template>
        </el-table-column>
        <el-table-column prop="remark" label="" align='center' width="200px"></el-table-column>
        <el-table-column prop="entry_time" label="" align='center'></el-table-column>
        <el-table-column label="" align='center'>
          <template slot-scope="scope">
            <el-button type="text" @click='examine(scope.row)' v-if='scope.row.state==2'></el-button>
            <el-button type="text" @click='showSee(scope.row)' v-if='scope.row.state==0'></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog title="" :visible.sync="dialogFormVisible" class='dialog' @close="cancel">
        <div class='btn'>
          <el-button type='primary' @click="check(1)" v-if="operationType==0"></el-button>
          <el-button type='primary' @click="showCheck(0)" v-if="operationType==0"></el-button>
          <el-button type='primary' @click='cancel'></el-button>
        </div>
        <div class='num_set'></div>
        <el-form v-model="examinePeople">
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>
              {{role(examinePeople.role)}}
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.account}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.department}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.position}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border  img'>
              <img src="../../../assets/images/head.png" />
              <!-- <img :src="'http://120.27.21.136:2798/' + examinePeople.img_url" /> -->
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.entry_time}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.project_name}}</div>
          </el-form-item>
          <div class='num_set'></div>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>
              {{sex(examinePeople.sex)}}
            </div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.tel}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.birth}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border width'>{{examinePeople.city_name+examinePeople.district_name+examinePeople.absolute_address}}</div>
          </el-form-item>
          <div class='num_set'></div>

          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{auditingState(examinePeople.state)}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.auditing_name}}</div>
          </el-form-item>
          <el-form-item class='input1'>
            <div></div>
            <div class='border'>{{examinePeople.auditing_time}}</div>
          </el-form-item>
          <!-- <el-form-item  class='input1'>
                    <div></div>
                    <div class='border'>{{examinePeople.tabsolute_addressel}}</div>
                </el-form-item> -->
          <el-form-item class='input1'>
            <div></div>
            <div class='border height'>{{examinePeople.remark}}</div>
          </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="" :visible.sync="refuseInfo" class='dialog' @close="cancelRefuseInfo">
        <el-form>
          <el-form-item label="">
            <el-input v-model="remark" class='textarea' type="textarea" />
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelRefuseInfo"> </el-button>
          <el-button type="primary" @click="check(0)"> </el-button>
        </div>
      </el-dialog>
    </template>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: [],
      searchObj: {
        tag_search: ""
      },
      dialogFormVisible: false,
      refuseInfo: false,
      examinePeople: {
        name: "",
        role: "",
        account: "",
        department: "",
        position: "",
        img_url: "",
        entry_time: "",
        project_name: "",
        sex: "",
        tel: "",
        birth: "",
        absolute_address: "",
        city_name: "",
        district_name: "",
        agent_id: null,
        project_id: null,
        id: null,
        remark: ""
      },
      submitForm: {},
      remark: "",
      operationType: 0 //0  1 
    };
  },

  mounted() {
    this.getExList();
  },
  methods: {
    showCheck(type) {
      this.refuseInfo = true;
    },
    
    async check(type) {
      this.submitForm.type = type;
      let temp = Object.assign({}, this.submitForm);
      if (type == 0) {
        temp.remark = this.remark;
        let res = await this.api.exPeople(temp);
        if (res.code == 200) {
          this.getExList();
          this.cancelRefuseInfo();
          this.cancel();
        }
      } else {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(async () => {
            this.$message({
              type: "success",
              message: "!"
            });
            let res = await this.api.exPeople(temp);
            if (res.code == 200) {
              this.getExList();
              this.cancelRefuseInfo();
              this.cancel();
            }
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    },
    async examine(row) {
      this.operationType = 0;
      this.dialogFormVisible = true;
      this.examinePeople = row;
      let res = await this.api.getExInfo(this.examinePeople);
      if (res.code == 200) {
        this.submitForm.project_id = row.project_id;
        this.submitForm.agent_id = row.agent_id;
        this.submitForm.id = row.id;
      }
    },
    async showSee(row) {
      this.operationType = 1;
      this.dialogFormVisible = true;
      Object.assign(this.examinePeople, row);
      let res = await this.api.getRefuseInfo(this.examinePeople);
      if (res.code == 200) {
        this.examinePeople = res.data;
      }
    },
    getRole(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    auditingState(row) {
      if (row == 0) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    role(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      }
    },
    sex(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 0) {
        return "";
      }
    },
    cancel() {
      this.dialogFormVisible = false;
    },
    cancelRefuseInfo() {
      this.refuseInfo = false;
      this.remark = "";
    },
    async getExList() {
      let res = await this.api.getExList();
      if ((res.code = 200)) {
        this.tableData = res.data.data;
      }
    },
    search(type) {
      this.searchObj.tag_search = type;
      this.getExList();
    }
  }
};
</script>





