<style lang="less" scoped src='./index.less'>
</style>
<style lang="less">
.maidInfo {
  .el-form-item {
    margin: 0;
  }
   .el-table th {
    padding: 5px 0px;
  }
    .el-table td {
    padding: 5px 0px;
  }
  .el-input__inner{
    height: 35px;
  }
 
}
</style>


<template>
  <div class='maidInfo'>
    <div class='table'>
      <div class='title'>
        <div class='title-text'></div>
        <el-button class='pos-btn-1' type="primary" @click='sumbit(2)'></el-button>
        <el-button class='pos-btn-2' type="primary" @click='sumbit(3)'></el-button>
        <el-button class='pos-btn' @click='cancel' type="primary"></el-button>
      </div>
      <el-form v-model="sumbitForm" class='form'>
        <el-form-item label="" class='row'>
          <el-input v-model="sumbitForm.batch_name" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <el-form-item label="" class='row'>
          <el-input v-model="sumbitForm.count_num" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <el-form-item label="" class='row'>
          <el-input v-model="no_price" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <!-- <el-form-item label="" class='row' v-if='operationType==1'>
          <el-input v-model="sumbitForm.allow" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <el-form-item label="" class='row' v-if='operationType==1'>
          <el-input v-model="sumbitForm.allow" auto-complete="off" class='input'></el-input>
        </el-form-item> -->
        <el-form-item label="" class='row'>
          <el-input v-model="sumbitForm.nail_name" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <el-form-item label="" class='row'>
          <el-input v-model="sumbitForm.nail_tel" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <el-form-item label="" class='row'>
          <el-input v-model="sumbitForm.second_name" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <el-form-item label="" class='row'>
          <el-input v-model="sumbitForm.second_tel" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <!-- <el-form-item label="" class='row' v-if='operationType==1'>
          <el-input v-model="sumbitForm.allow" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <el-form-item label="" class='row' v-if='operationType==1'>
          <el-input v-model="sumbitForm.allow" auto-complete="off" class='input'></el-input>
        </el-form-item>
        <el-form-item label="" class='row' v-if='operationType==1'>
          <el-input v-model="sumbitForm.allow" auto-complete="off" class='input'></el-input>
        </el-form-item> -->
      </el-form>

      <div class='title-text'></div>
      <el-table :data="tableData" border style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align='center'></el-table-column>
        <el-table-column prop="client_id" label="" align='center' width="120px"></el-table-column>
        <el-table-column prop="name" label="" align='center'></el-table-column>
        <el-table-column prop="tel" label="" align='center' ></el-table-column>
        <el-table-column prop="broker_type" label="" align='center' width="180px">
          <template slot-scope="scope">{{type(scope.row.broker_type)}}</template>
        </el-table-column>
        <el-table-column prop="create_time" label="" align='center'></el-table-column>
        <el-table-column prop="broker_num" label="" align='center'></el-table-column>
        <!-- <el-table-column prop="project_hold_phone" label="" align='center' v-if='operationType==1'></el-table-column>
        <el-table-column prop="all_price" label="" align='center' v-if='operationType==1'></el-table-column> -->
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      sumbitForm: {
        batch_name: "",
        nail_name: "",
        nail_tel: "",
        second_name: "",
        second_tel: "",
        broker_id: [],
        state: 3,
        count_num: ""
      },
      no_price: 0,
      rule_id: "",
      project_id: "",
      operationType: 0 // 1   0 
    };
  },
  mounted() {
    this.project_id = this.$route.params.project_id;
    this.operationType = this.$route.params.operationType;
    if (!this.project_id) {
      this.$router.push({ name: "distri_commission" });
      return;
    }
    this.rule_id = this.$route.params.rule_id;
    this.getBrokerApply();
  },
  methods: {
    async sumbit(state) {
      let temp = Object.assign({}, this.sumbitForm);
      temp.project_id = this.project_id;
      temp.state = state;
      let res = await this.api.brokerApply(temp);
      if (res.code == 200) {
         this.$router.push({ name: "distri_commission" });
      }
    },
    async getBrokerApply() {
      let res = await this.api.getBrokerApply({
        project_id: this.project_id,
        rule_id: this.rule_id
      });
      if (res.code == 200) {
        this.tableData = res.data;
      }
    },
    type(row) {
      if (row == 1) {
        return "";
      } else if (row == 2) {
        return "";
      } else if (row == 3) {
        return "";
      }
    },
    cancel() {
      this.$router.push({
        name: "distri_commission"
      });
    },
    handleSelectionChange(val) {
      let arr = [];
      let count = 0;
      for (let v of val) {
        count += v.broker_num;
        arr.push(v.broker_id);
      }
      this.sumbitForm.broker_id = arr.join(",");
      this.sumbitForm.count_num = arr.length;
      this.no_price = count;
    }
  }
};
</script>




